import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import MyButton from '../../util/MyButton';
import withStyles from '@material-ui/core/styles/withStyles';
import dayjs from 'dayjs';
import { Link } from 'react-router-dom';
import LikeButton from './LikeButton';
import Comments from '../scream/Comments';
import CommentForm from '../scream/CommentForm';
// import DeleteComment from '../scream/DeleteComment';

// MUI stuff
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import CircularProgress from '@material-ui/core/CircularProgress';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';


// Icons
import CloseIcon from '@material-ui/icons/Close';
import UnfoldMore from '@material-ui/icons/UnfoldMore';
import ChatIcon from '@material-ui/icons/Chat';

// Redux Stuff
import { connect } from 'react-redux';
import { getScream, clearErrors} from '../../redux/actions/dataActions';



const styles = (theme) => ({
...theme.themeObject,

hiddenSeparator: {
    border: 'none',
    margin: 4
},
profileImage: {
maxWidth: 200,
height: 200,
objectFit: 'cover',
borderRadius: '50%'
},
dialogContent: {
 padding: 20 
},
closeButton: {
 position: 'absolute',
 left: '90%'   
},
expandButton: {
    position: 'absolute',
    left: '90%'
},
spinnerDiv: {
    textAlign: 'center',
    marginTop: 50,
    marginBottom: 50
}
});


class ScreamDialog extends Component {

state = {
    open: false,
    oldPath: '',
    newPath: ''
}

componentDidMount() {
    if(this.props.openDialog) {
        this.handleOpen();
    }
}
handleOpen = () => {

let oldPath = window.location.pathname;
const { userHandle, screamId } = this.props;
const newPath = `/user/${userHandle}/article/${screamId}`;

if(oldPath === newPath) oldPath = `/user/${userHandle}`;

window.history.pushState(null, null, newPath);

this.setState({ open: true, oldPath, newPath });
    this.props.getScream(this.props.screamId);
};

handleClose = () => {

window.history.pushState(null, null, this.state.oldPath);
this.setState({ open: false});
    this.props.clearErrors();
}
    render() {
    const { 
        classes, 
        scream: { 
        screamId, 
        body, 
        createdAt, 
        likeCount, 
        commentCount, 
        userImage, 
        userHandle, 
        comments
    }, 
    UI: {loading} } = this.props;

    const dialogMarkup = loading ? (
        <div className={classes.spinnerDiv}>
         <CircularProgress size={200} thickness={2} />   
         </div>    
         ) : (
        <Grid container spacing= {16}>
        <Grid item sm={5}>
        <img src = { userImage } 
        alt="Profile" 
        className={ classes.profileImage } />
        </Grid>
        <Grid item sm={7}>
        <Typography
        component={Link}
        variant="h5"
        color="primary"
        to = {`/user/${userHandle}`} >
         @{userHandle}   
        </Typography>
        <hr className={ classes.hiddenSeparator } />
<Typography variant="body2" color="textSecondary"> 
{ dayjs(createdAt).format('MMMM DD YYYY h:mm a' ) }   
        </Typography>
        <hr className={classes.hiddenSeparator} />
        <Typography variant="body1">{ body }</Typography>
        <LikeButton screamId={screamId} />
        <span>{likeCount} likes</span>
        <MyButton tip="comments">
        <ChatIcon color="primary" />
        </MyButton>
        <span>{commentCount} comments</span>
        </Grid>
        <hr className={classes.visibleSeparator} />
        <CommentForm screamId={screamId} />
        <Comments comments={comments} /> 
        {/* <DeleteComment commentId={commentId} /> */}
        </Grid>
    )
        return (
          <Fragment>
        <MyButton 
        onClick={this.handleOpen} 
        tip="Expand post!" 
        tipClassName={classes.expandButton}>
        <UnfoldMore color="primary" />
        </MyButton>
        <Dialog 
        open={this.state.open} 
        onClose={this.handleClose}
        fullWidth
        maxWidth = "sm"
        >
        <MyButton 
        onClick={this.handleClose} 
        tip="Close" 
        tipClassName={classes.closeButton}>
        <CloseIcon />
        </MyButton>
        <DialogContent 
        className = {classes.dialogContent} />
        { dialogMarkup }
        </Dialog>
          </Fragment>
        )
    }
}

ScreamDialog.propTypes = {
    clearErrors: PropTypes.func.isRequired,
    getScream: PropTypes.func.isRequired,
    classes: PropTypes.object.isRequired,
    screamId: PropTypes.string.isRequired,
    userHandle: PropTypes.string.isRequired,
    scream: PropTypes.object.isRequired,
    UI: PropTypes.object.isRequired

}

const mapStateToProps = (state) => ({
    scream: state.data.scream,
    UI: state.UI
})

const mapActionsToProps = {
    getScream,
    clearErrors
}
export default connect(mapStateToProps, mapActionsToProps)(withStyles(styles)(ScreamDialog))
